@charset "UTF-8";

.article::before,
.article::after {
  content: '';
  display: table;
}

.article::after {
  clear: both;
}

#header .container,
.reviews .review-author {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn,
a.btn {
  text-align: center;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}

[class*=icon-],
.link-all span,
.link-back span,
.filter-form .btn-extra-options,
.filter-form .btn-extra-options span,
.reviews-list .review .likes span {
  display: inline-block;
  vertical-align: middle;
}

.article ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.slick-dots {
  padding: 0;
  margin: 1rem 0 0 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  padding: 0;
  cursor: pointer;
  line-height: 1;
  vertical-align: top;
  margin: 0 0.3rem;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 0.625rem;
  height: 0.625rem;
  border: 0.125rem solid rgba(21, 21, 21, 0.1);
  padding: 0;
  cursor: pointer;
  color: transparent;
  border-radius: 50%;
  outline: none;
  background: none;
  margin: 0;
}

.slick-dots li:hover button,
.slick-dots li.slick-active button {
  background-color: #ffdd2d;
  border: none;
}

.quick-search-form .search-results,
#header .container #nav > ul > li ul,
.drop-list {
  opacity: 0;
  visibility: hidden;
  transition: 0.3s all;
}

.quick-search-form .search-results.active,
#header .container #nav > ul > li.active ul,
#header.show-menu .container #nav,
#header.show-menu .container #quick-search,
.drop-list.active {
  opacity: 1;
  visibility: visible;
}

.quick-search-form .search-results,
#header .container #nav > ul > li ul,
.drop-list {
  position: absolute;
  right: 0;
  top: calc(100% + 1.5rem);
  box-shadow: 0 0.25rem 1rem rgba(21, 21, 21, 0.1);
  border-radius: 0.25rem;
  background-color: #ffffff;
  overflow: hidden;
}

.quick-search-form .search-results > li,
#header .container #nav > ul > li ul > li,
.drop-list > li {
  font-size: 0.875rem;
  padding: 0.75rem 1rem;
}

.quick-search-form .search-results > li > a,
#header .container #nav > ul > li ul > li > a,
.drop-list > li > a {
  display: block;
  padding: 0.75rem 1rem;
  margin: -0.75rem -1rem;
}

.quick-search-form .search-results > li:hover,
#header .container #nav > ul > li ul > li:hover,
.drop-list > li:hover,
.quick-search-form .search-results > li.active,
#header .container #nav > ul > li ul > li.active,
.drop-list > li.active {
  background: #f8f8f8;
}

.quick-search-form .search-results > li:hover > a,
#header .container #nav > ul > li ul > li:hover > a,
.drop-list > li:hover > a,
.quick-search-form .search-results > li.active > a,
#header .container #nav > ul > li ul > li.active > a,
.drop-list > li.active > a {
  color: #151515;
}

.quick-search-form .search-results > li:last-child,
#header .container #nav > ul > li ul > li:last-child,
.drop-list > li:last-child {
  border-bottom: 0;
}

#products-main #slider-products-main .product,
.offers .offer,
.reviews .review {
  padding: 1.5rem;
  background: #fff;
  border-radius: 0.5rem;
}

.slider-items,
#products-main #slider-products-main {
  margin: -.75rem;
}

.slider-items .slick-slide,
#products-main #slider-products-main .slick-slide {
  padding: .75rem;
}

.nav-links a,
.nav-links span,
#card figure,
#card-reviews figure {
  display: flex;
  align-items: center;
  justify-content: center;
}

.offers-list .offer-options dl,
#card .card-options dl {
  line-height: 1.5rem;
}

.offers-list .offer-options dl dt,
#card .card-options dl dt {
  font-size: 0.875rem;
  white-space: nowrap;
}

.offers-list .offer-options dl dd,
#card .card-options dl dd {
  color: #151515;
  font-weight: 500;
}

.offers-list .offer-options dl dd img,
#card .card-options dl dd img {
  margin-right: 1rem;
  display: inline-block;
}

.offers-list .offer-options dl dd img:last-child,
#card .card-options dl dd img:last-child {
  margin: 0;
}

.offers-list .offer-options dl.v-2,
#card .card-options dl.v-2 {
  color: rgba(21, 21, 21, 0.3);
  font-size: 0.875rem;
}

.offers-list .offer-options dl.v-2 dd,
#card .card-options dl.v-2 dd {
  font-size: 0.875rem;
  color: rgba(21, 21, 21, 0.3);
}

/* reset browser styles */

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section,
main {
  display: block;
}

body {
  line-height: 1.2;
}

ol {
  padding-left: 16px;
  list-style: decimal;
}

ul {
  padding-left: 16px;
  list-style: disc;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
:after,
:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

