/*   */
.ui-slider {
    transition: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

/*  */
.ui-slider .ui-slider-handle {
    position: absolute;
    width: 0.875rem;
    height: 0.875rem;
    box-shadow: 0 0 0 $rounded rgba(255, 221, 45, 0.3);
    background-color: #ffffff;
    z-index: 10;
    cursor: pointer;
    font-size: 11px;
    transition: none;
    white-space: nowrap;
    border-radius: 50%;
}

.ui-slider .ui-slider-range {
    position: absolute;
    z-index: 1;
    font-size: .7em;
    display: block;
    border: 0;
    overflow: hidden;
    transition: none;
}

/*   (     ) */
.ui-slider-horizontal {
    height: 0.188rem; /*     */
}

/*   */
.ui-slider-horizontal .ui-slider-handle {
    top:  -5px;
    margin-left: -7px;
}

.ui-slider-horizontal .ui-slider-range {
    top: 0;
    height: 100%;
}

.ui-slider-horizontal .ui-slider-range-min {
    left: 0;
}

.ui-slider-horizontal .ui-slider-range-max {
    right: 0;
}

/*       */
.ui-widget-content {
    background: #fff;
}

/*    (  ) */
.ui-widget-header {
    background: $yellow;
}

/*     */
.ui-corner-all {
    border-radius: 7.5px;
}
